<template>
    <div class="login">

        <HomeHeader :topbanner="topbanner"></HomeHeader>


        <div class="top-head">
            <span class="go-back" @click="back()"></span>
            <span class="go-regis" @click="goRegis"></span>
        </div>


        <ul class="input_box">

            <li><input type="text" placeholder="//" autofocus v-model="phone"/></li>
            <li><input type="password" placeholder=""  v-model="password"/></li>

        </ul>   


        <div class="next-step" @click="login()"></div>

        <div class="msgbox" v-show="msg">{{msg}}</div>


        
        
    </div>
</template>


<script>
    import HomeHeader from '../common/HomeHeader'
    export default {
        data(){
            return {
                msg:"",
                phone:"",
                password:""
            }
            
        },

         methods:{
            back(){
                window.history.back();
            },
            //
            login(){
                this.$http.get("http://139.199.107.198:8888/login",{
                    params:{
                        user:this.phone,
                        password:this.password
                    }
                }).then(res=>{
                    if(res.body=="1"){
                        //
                        this.loginSuccess();

                        //jp_cookie
                        this.saveCookie();
                        //
                        this.getCartData();
                        // vuex username
                        this.$store.commit("CHANGE_USERNAME","jp_"+this.phone);
                    }else{
                        //
                        this.loginError();
                    }
                })
            },
            goRegis(){
                this.$router.push({name:'Register'})
            },

            loginSuccess(){
                var _this=this
                this.msg="";
                setTimeout(function(){
                    _this.msg="";
                },2000);
            },
            loginError(){
                var _this=this
                this.msg="";
                setTimeout(function(){
                    _this.msg="";
                },2000);
            },
            saveCookie(){
                var oDate = new Date();
                // cookie
                oDate.setDate(oDate.getDate() + 30);
                // cookie
                document.cookie="html1705=jp_"+this.phone+";expires=" + oDate;
                console.log(document.cookie)
                // var arr=document.cookie.split(";")//;cookie
                // var obj={};//cookie
                
                // arr.forEach(function(item,index){
                //     //,=
                //     //cookie,trim
                //     var a=item.trim().split("="); 
                //     obj[a[0]]=a[1];//key,value
                // })
                // console.log(obj);
            },
            //
            getCartData(){
                this.$http.get("http://139.199.107.198:8888/getCart",{
                    params:{
                        username:"jp_"+this.phone
                    }
                }).then(res=>{
                    console.table(res.body);
                    var _this=this;
                    //vuex
                    this.$store.commit("ISLOGINDATA",res.body);
                    //
                    this.$store.commit("CHANGEISLOGIN",true)
                    
                    setTimeout(function(){
                        //
                        _this.$router.push({name:'Mine'})
                    },2500)
                })
            }
            
         },

         computed:{
            topbanner:function(){
                return this.$store.getters.topBanner
            }
        },
        components:{
           HomeHeader
        }
    }
</script>


<style lang="scss" scoped>
    .login{
        background: white;
        height: 1000px;
    }
    .top-head{
        height: 1.8rem;
        font-size:18px;
        line-height: 1.8rem;
        text-align: center;
        color:white;
        position:relative;
        background: #ff464e;
        border-bottom:1px solid #f2f2f2;
        .go-back{
            position:absolute;
            width:24px;
            height: 24px;
            background:url(../assets/mine_back.png)no-repeat;
            background-size: 24px 24px;
            left: .5rem;
            top: .4rem;
        }
        .go-regis{
            font-size:14px;
            float: right;
            color: white;
            height: 1.8rem;
            margin-right: .3rem;
        }
    }


    .input_box{
        margin-bottom:.7rem;
        li{
            width: 94%;
            border-bottom:2px solid #f2f2f2;
            margin:0 auto;
            input{
                border:0;
                width:100%;
                height: 1.8rem;
                font-size:14px;
            }
        }
    }


    .next-step{
        text-align: center;
        height: 1.8rem;
        background: #ff464e;
        line-height: 1.8rem;
        width: 96%;
        margin:0 auto;
        font-size:18px;
        color: white;
    }


    .msgbox{
        position:fixed;
        bottom:20rem;
        height: 2.5rem;
        width: 100%;
        background: rgba(0,0,0,.6);
        margin:0 auto;
        color: white;
        line-height: 2.5rem;
        text-align: center;
        font-size:15px;
    }
</style>




