<template>
    <div id="app">
    
        <router-view></router-view>
        
    
    </div>
</template>

<script>
export default {
    name: 'app',
    data() {
        return {
        }
    },
    methods: {
        //
        getMenuData() {
            this.$http.get('../static/json/menubar.json').then(function (res) {
                this.$store.commit('ADD_MENUBARDATA',res.body.menu_list)//vuex mutations
                this.$store.commit('ADD_TOPBanner',res.body.topBanner)//vuex mutations

            })
        },
        //cookiehtml1705:jy_xxxxx
        checkCookie(){
            //strvuex
                console.log(document.cookie);
                var str=document.cookie.split("=")[1];
                if(str){

                    this.$http.get("http://139.199.107.198:8888/getCart",{
                        params:{
                            username:str
                        }
                    }).then(res=>{
                        // console.table(res.body)
                        //vuex
                        this.$store.commit("ISLOGINDATA",res.body);
                        //
                        this.$store.commit("CHANGE_USERNAME",str);
                        //
                        this.$store.commit("CHANGEISLOGIN",true);
                    })
                }else{
                    console.log("cookie")
                }
        //     var arr=document.cookie.split(";")//;cookie
        //         var obj={};//cookie
                
        //         arr.forEach(function(item,index){
        //             //,=
        //             //cookie,trim
        //             var a=item.trim().split("="); 
        //             obj[a[0]]=a[1];//key,value
        //         })
        //         console.log(obj);
            },

        },
    created() {
        //cookie
        this.checkCookie()
        this.getMenuData()
    }

}
</script>


